import React from 'react';
import { Line } from 'react-chartjs-2';

function Chart({ data }) {
  const chartData = {
    labels: data.map(item => item.label),
    datasets: [
      {
        label: 'Data',
        data: data.map(item => item.value),
        fill: false,
        borderColor: 'rgb(75, 192, 192)',
        tension: 0.1
      }
    ]
  };

  const chartOptions = {
    responsive: true,
    interaction: {
      intersect: false,
    },
    scales: {
      x: {
        display: true,
        title: {
          display: true,
          text: 'X Axis'
        }
      },
      y: {
        display: true,
        title: {
          display: true,
          text: 'Y Axis'
        }
      }
    }
  };

  return (
    <div>
      <Line data={chartData} options={chartOptions} />
    </div>
  );
}

export default Chart;
